/* Details Tabs */

.tabs {
  position: relative;
  /*	height: 774px; */
  height: 103vh;

  clear: both;
  border-bottom-right-radius: 20px;
  -moz-border-radius-bottomright: 20px;
  -webkit-border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  -moz-border-radius-bottomleft: 20px;
  -webkit-border-bottom-left-radius: 20px;
  margin-top: 25px;
  margin-right: 0;
  margin-bottom: 25px;
  margin-left: -1px;
}

.tab {
  float: left;
}

.tab label {
  background: rgb(215, 233, 235); /* Old browsers */
  background: -moz-linear-gradient(top, rgba(215, 233, 235, 1) 0%, rgba(181, 216, 226, 1) 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(215, 233, 235, 1)), color-stop(100%, rgba(181, 216, 226, 1))); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, rgba(215, 233, 235, 1) 0%, rgba(181, 216, 226, 1) 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, rgba(215, 233, 235, 1) 0%, rgba(181, 216, 226, 1) 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(top, rgba(215, 233, 235, 1) 0%, rgba(181, 216, 226, 1) 100%); /* IE10+ */
  background: linear-gradient(to bottom, rgba(215, 233, 235, 1) 0%, rgba(181, 216, 226, 1) 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#d7e9eb', endColorstr='#b5d8e2', GradientType=0); /* IE6-9 */
  padding: 10px;
  border: 1px solid #ccc;
  margin-left: -1px;
  position: relative;
  left: 1px;
}

.tab [type=radio] {
  display: none;
}

.content {
  position: absolute;
  top: 25px;
  left: 0;
  background: white;
  right: 0;
  bottom: 0;
  padding: 20px;
  border: 1px solid #ccc;
  border-bottom-left-radius: 20px;
  -moz-border-radius-bottomleft: 20px;
  -webkit-border-bottom-left-radius: 20px;

  border-bottom-right-radius: 20px;
  -moz-border-radius-bottomright: 20px;
  -webkit-border-bottom-right-radius: 20px;
}

.content2 {
  position: absolute;
  top: 25px;
  left: 0;
  background: white;
  right: 0;
  bottom: 0;
  padding: 20px;
  border: 1px solid #ccc;
  border-bottom-left-radius: 20px;
  -moz-border-radius-bottomleft: 20px;
  -webkit-border-bottom-left-radius: 20px;
}

.tabs [type=radio]:checked ~ label {
  background: white;
  border-bottom: 1px solid white;
  z-index: 2;
}

.tabs [type=radio]:checked ~ label ~ .content {
  z-index: 1;
}

.tabs [type=radio]:checked ~ label ~ .content2 {
  z-index: 1;
}

