.main-dropdown {
  display: none;
  background: white;
  border: 1px solid #a6ce5d;
  height: auto;
  width: 180px;
  position: absolute;
  right: 10px;
  top: 125px;
  border-radius: 5px;
  box-shadow: 1px 2px 3px lightgray;
  transition: 0.5s;
  text-align: left;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  z-index: 9;
}

#reporting-menu, #library-menu {
  padding-bottom: 10px;
}

#reporting-menu:hover + .main-dropdown, #library-menu:hover + .main-dropdown {
  display: inline-block;
}

.main-dropdown:hover {
  display: inline-block;
}

.main-dropdown > a {
  color: #333 !important;
  display: block;
  line-height: 20px;
  margin: 5px 0 10px 0;
}

.main-dropdown > a:hover {
  text-decoration: underline !important;
}

.renew-modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 99999999;
  background: rgba(0, 0, 0, 0.6);
}

.renew-modal-card {
  background: #ffffff;
  border-radius: 6px;
  padding: 1rem;
  display: block;
}


